<template>
  <div class="area-main">
    
    <div class="calendar">
      <div
        v-for="points in calendar"
        :key="points.stamp"
        class="day"
      >
        <div class="points-info">
          <div class="points"><div class="star svg-star1"/>{{points.win}}</div>
          <div class="points extra"><div class="star svg-star2"/>{{points.extra}}</div>
        </div>
        <div class="date">{{points.date}}</div>
      </div>
    </div>
    <ControlsPanelBase>
      <template #controls-first>
        <div 
          @click="this.buttonBackHandler()"
          class="tile-round bg-blue svg-back">
        </div>
        <CurrentProfile/>
      </template>
      <template #controls-last>
        <div
          class="bg-blue tile-round svg-recyle-bin"
          @click="this.removeProfileHandler()"
        >
        <svg 
            width="100%"
            height="100%"
            viewBox="0 0 800 800"
            style="transform:rotate(-90deg); position: absolute;"
          >
            <circle
              r="350"
              cx="400"
              cy="400"
              fill="transparent"
              stroke="#ffffff"
              stroke-linecap2="round"
              stroke-width="100"
              stroke-dasharray="2198"
              :stroke-dashoffset="`${Math.floor(-2198*(1-(removeIntervalValue-200)/200))}`"
            />
          </svg>
        </div>
      </template>
    </ControlsPanelBase>
  </div>
</template>

<script>
import DomMixin from '@/mixins/domMixin';
import ControlsPanelBase from '@/components/ControlsPanelBase';
import ProfileMixin from '@/mixins/profileMixin';
import CurrentProfile from '@/components/CurrentProfile';
import { getDateFromStamp, getDateStamp } from '@/utils'
export default {
    mixins: [
      DomMixin,
      ProfileMixin,
    ],
    created() {
      this.domSetDocumentBackgroundUrl(require('/public/img/bg-empty1-1080p.png'));
    },
    beforeMount() {
      this.currentProfile = this.getProfile(this.getCurrentProfile())

      let calendarObj = {}
      const today = getDateStamp()
      const points = this.currentProfile?.points || {}
      // prepare date stamps
      const stampList = [...new Set(Object.keys(points)
        .map(v => parseInt(v.split('_')[0]))
        .filter(v => v <= today)
      )] || []
      .slice(-30)
      //calculate points
      Object.keys(points)
        .forEach(v => {
          const stamp = parseInt(v.split('_')[0])
          if (stampList.includes(stamp)) {
            const date = getDateFromStamp(stamp)
            calendarObj[stamp] = {
              stamp: stamp,
              date: `${('0'+date.getUTCDate()).slice(-2)}.${('0'+(1+date.getUTCMonth())).slice(-2)}`,
              win: (points[v][0] ?? 0) + (calendarObj[stamp]?.win ?? 0),
              extra: (points[v][1] ?? 0) + (calendarObj[stamp]?.extra ?? 0)
            }
          }
        })
      this.calendar = Object.values(calendarObj).sort((a,b) => a.stamp - b.stamp)
    },
    components: {
      ControlsPanelBase,
      CurrentProfile,
    },
    data() {
      return {
        currentProfile: null,
        calendar: [],
        
        removeInterval: null,
        removeIntervalValue: 200,
      }
    },
    beforeUnmount() {
      this.resetTimer()
    },
    methods: {
      buttonBackHandler() {
        this.$router.push({ name: 'home' })
      },
      removeProfileHandler() {
        this.removeInterval ? this.resetTimer() : this.startTimer()
      },
      removeProfile() {
        this.setCurrentProfile(null);
        this.deleteProfile(this.currentProfile.id);
        this.buttonBackHandler();
      },
      resetTimer() {
        if (this.removeInterval) {
          clearInterval(this.removeInterval)
          this.removeInterval = null
          this.removeIntervalValue = 200
        }
      },
      startTimer() {
        if (!this.removeInterval) {
          this.removeInterval = setInterval(this.tickTimer, 50)
        }
      },
      tickTimer() {
        if (this.removeIntervalValue > 0) {
          this.removeIntervalValue -= 1
        } else {
          this.resetTimer()
          this.removeProfile()
        }
      },
    },
}
</script>

